import { useState } from "react";

const Projects = () => {
  // State to hold the selected language
  const [selectedLanguage, setSelectedLanguage] = useState("All");

  // Example projects (replace with your actual projects)
  const projects = [
    { id: 1, name: "Project 1", language: "JavaScript" },
    { id: 2, name: "Project 2", language: "React" },
    { id: 3, name: "Project 3", language: "Python" },
    { id: 4, name: "Project 4", language: "Nextjs" },
    { id: 5, name: "Project 5", language: "React" },
    { id: 6, name: "Project 6", language: "Python" },
  ];

  // Function to filter projects by selected language
  const filterProjects = (language) => {
    if (language === "All") {
      return projects;
    }
    return projects.filter(project => project.language === language);
  };

  return (
    <div className="p-4">
      {/* Navbar for selecting language */}
      <div className="flex space-x-4 mb-6">
        <button onClick={() => setSelectedLanguage("All")} className="p-2 bg-blue-500 text-white rounded">
          All
        </button>
        <button onClick={() => setSelectedLanguage("JavaScript")} className="p-2 bg-blue-500 text-white rounded">
          JavaScript
        </button>
        <button onClick={() => setSelectedLanguage("React")} className="p-2 bg-blue-500 text-white rounded">
          React
        </button>
        <button onClick={() => setSelectedLanguage("Python")} className="p-2 bg-blue-500 text-white rounded">
          Python
        </button>
      </div>

      {/* Display projects based on selected language */}
      <div>
        <h2 className="text-2xl font-bold mb-4">Projects in {selectedLanguage}</h2>
        <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 gap-4">
          {filterProjects(selectedLanguage).map(project => (
            <div key={project.id} className="p-4 border rounded-lg shadow-lg">
              <h3 className="text-xl font-semibold">{project.name}</h3>
              <p className="text-sm text-gray-500">Language: {project.language}</p>
            </div>
          ))}
        </div>
      </div>
    </div>
  );
};

export default Projects;
